import React from "react"
import "./LoginPage.scss"
import LogoIcon from "../../assets/icons/LogoIcon.svg"
import { Button } from "../common/Button/Button"
import { TextField } from "../common/AdminForms/AdminForms"
import { Form, Formik } from "formik"
import { connect } from "react-redux"
import { Redirect } from "react-router-dom"
import { logIn } from "../../store/auth-reducer"
import * as Yup from "yup"
import Cookies from "js-cookie"

const initialValues = {
  username: "",
  password: "",
}

const validationSchema = Yup.object({
  username: Yup.string().required("–í–≤–µ–¥–∏—Ç–µ –ª–æ–≥–∏–Ω"),
  password: Yup.string().required("–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"),
})

const LoginPage = ({ logIn, isAuthInProgress, isAuthFailed }) => {
  const accessToken = Cookies.get("access_token")
  if (accessToken) {
    return <Redirect to="/admin/orders" />
  }

  const onLogInSubmit = (userData) => {
    logIn(userData)
  }

  return (
    <div className="login__wrapper">
      <div className="login">
        <div className="login__logo">
          <img src={LogoIcon} alt="Logo" className="logo__img" />
          <h1 className="logo__title">Need for drive</h1>
        </div>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onLogInSubmit}
        >
          {(formik) => {
            return (
              <Form className="login-form">
                <div className="login-form__title">–í—Ö–æ–¥</div>
                {isAuthFailed && (
                  <span className="error-message">
                    –ù–µ–≤–µ—Ä–Ω—ã–π –ª–æ–≥–∏–Ω –∏–ª–∏ –ø–∞—Ä–æ–ª—å
                  </span>
                )}
                <TextField
                  name="username"
                  title="–ü–æ—á—Ç–∞"
                  placeholder="admin@ss.com"
                  type="text"
                />
                <TextField
                  name="password"
                  title="–ü–∞—Ä–æ–ª—å"
                  placeholder="password"
                  type="password"
                />
                <div className="login-form__footer">
                  <div className="login-form__access">–ó–∞–ø—Ä–æ—Å–∏—Ç—å –¥–æ—Å—Ç—É–ø</div>
                  <Button
                    type="submit"
                    additionalStyles="button__admin"
                    disabled={!formik.isValid}
                    isLoading={isAuthInProgress}
                  >
                    –í–æ–π—Ç–∏
                  </Button>
                </div>
              </Form>
            )
          }}
        </Formik>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  isAuthInProgress: state.auth.isAuthInProgress,
  isAuthFailed: state.auth.isAuthFailed,
})

export default connect(mapStateToProps, { logIn })(LoginPage)
